<template>
  <div
    style="font-size:0.8rem;"
    class="t10"
  >
    <div
      class="settle"
      @click="btnmoneydetail"
      style="border-bottom:none;"
    >
      <p>结算金额</p>
      <p>&yen; {{ money }} ></p>
    </div>
    <div class="payStatu">
      <p class="wx">
        <span>微信结算：</span>
        <span>
          <span>{{item.wxBalanceAmount}}</span>
          <span v-if="item.wxPayStatus == 0">未转账</span>
          <span
            :class="'statu'+item.wxPayStatus"
            v-else
          >{{item.wxPayStatus == 2?'失败':'成功'}}</span>
        </span>
      </p>
      <p class="wx">
        <span>支付宝结算：</span>
        <span>
          <span>{{item.aliBalanceAmount}}</span>
          <span v-if="item.aliPayStatus == 0">未转账</span>
          <span
            :class="'statu'+item.aliPayStatus"
            v-else
          >{{item.aliPayStatus == 2?'失败':'成功'}}</span>
        </span>
      </p>
    </div>
    <div>
      <div
        class="all"
        v-for="item in lists_long"
        :key="item.id"
      >
        <span>{{ item.name }}</span>
        <span>&yen;{{ item.money }}</span>
      </div>
    </div>
    <mt-button
      v-if="item.aliPayStatus == 2||item.wxPayStatus==2"
      @click="reset"
      type="primary"
      style="width:90%;margin:20px;font-size:16px;font-weight:normal;background-color:#f60;color:#fff;"
    >重新转账</mt-button>
  </div>
</template>
<script>
import '@/assets/styles/global.css';
import { GetBusinessMonthBillRecordTransfer } from '@/api/api_business_hotel_and_scale';
import { MessageBox, Toast } from 'mint-ui';
export default {
    data() {
        return {
            item: '',
            strat: '',
            end: '',
            lists: [
                { id: 0, name: '房费收入', money: 0 },
                { id: 1, name: '消费收入(扣押金)', money: 0 },
                { id: 2, name: '承担退款', money: 0 },
                { id: 3, name: '连住提前退房', money: 0 },
                { id: 4, name: '冲账', money: 0 }
            ],
            lists_long: [
                { id: 0, name: '智能电表收入', money: 0 },
                { id: 1, name: '房租收入', money: 0 },
                { id: 2, name: '生活费用收入', money: 0 }
            ],
            money: '',
            id: ''
        };
    },
    created() {
        this.$store.commit('setHeader', ' 账单详情');
        this.item = JSON.parse(localStorage.getItem('list'));
        this.money = this.item.totalPrice.toFixed(2);
        this.lists_long[0].money = this.item.elecAmount ? this.item.elecAmount : '0';
        this.lists_long[1].money = this.item.roomAmount ? this.item.roomAmount : '0';
        this.lists_long[2].money = this.item.lifeAmount ? this.item.lifeAmount : '0';
    },
    methods: {
        // 跳转至订单列表
        btnmoneydetail() {
            let obj = {};
            obj.id = this.item.id;
            obj.num = this.item.selected;
            this.$router.push({
                name: 'MoneyDetail',
                query: {
                    billRecordId: this.$route.query.billRecordId,
                    selected: this.item.selected
                }
            });
        },
        // 重新转账
        reset() {
            // 长租
            MessageBox.confirm('', {
                message: '是否重新转账？',
                title: '提示',
                cancelButtonText: '否',
                confirmButtonText: '是'
            })
                .then((action) => {
                    if (action == 'confirm') {
                        GetBusinessMonthBillRecordTransfer(this.item.id).done((res) => {
                            Toast({
                                message: res.msg,
                                duration: 900
                            });
                            setTimeout('window.history.go(-1)', 900);
                        });
                    }
                })
                .catch((e) => {
                    console.log(e);
                });
        }
    }
};
</script>
<style scoped>
.payStatu {
    background: #fff;
    text-align: right;
    border-bottom: 1px solid #eee;
    color: #9e9e9e;
    line-height: 26px;
    padding: 0 8px;
}
.payStatu > .wx {
    display: flex;
    justify-content: space-between;
}
.payStatu > .wx > span:nth-child(1) {
    display: inline-block;
    text-align: left;
    color: #000;
}
.payStatu > .wx > span:nth-child(2) {
    width: 50%;
}
.payStatu > .wx > span:nth-child(2) > span:nth-child(1) {
    color: #000;
    text-align: left;
    width: 30%;
    display: inline-block;
}
.payStatu > .wx > span:nth-child(2) > span:nth-child(2) {
    width: 30%;
    display: inline-block;
}
</style>



