<template>
  <div id="finance">
    <!-- 营收日报---日账单与月账单 -->
    <div v-if="$route.query.type == 'Paid'">
      <div
        class="priceBox mt6 p6"
        v-if="Billdata"
      >
        <div class="title p5">
          <span class="three_color"></span>
          <span>营收总计&ensp;(元)</span>
        </div>
        <div style="margin: 0rem 0px 0.8rem;">
          <span class="f_20">{{ Billdata.totalPrice | priceToFixed}}</span>
        </div>
        <div
          class="display"
          v-if="$route.query.selected == 1"
        >
          <div class="w24">
            <div class="c_98">房费收入</div>
            <div class="f84 left">{{ Billdata.roomAmount | priceToFixed }}</div>
          </div>
          <div class="w24">
            <div class="c_98">非房费收入</div>
            <div class="f84 left">{{ Number(Billdata.depositAmount) + Number(Billdata.extraAmount) | priceToFixed }}</div>
          </div>
          <div class="w24">
            <div class="c_98">支出</div>
            <div class="f84 left">{{ Number(Billdata.reverseAmount) + Number(Billdata.refuseAmount) | priceToFixed }}</div>
          </div>
        </div>
        <div v-else-if="$route.query.selected == 2">
          <div
            v-for="(item,i) in longType"
            :key="i"
            class="aaa"
          >
            <div>
              <div class="c_98">{{ item.name }}</div>
              <div class="f84 left">{{ item.price | priceToFixed }}</div>
            </div>
          </div>
        </div>
      </div>
      <!-- 结算总价 -->
      <div
        class="priceBox mt6 p6"
        v-if="Billdata"
      >
        <div class="title p5">
          <span
            class="three_color"
            style="background: #FF7D41;"
          ></span>
          <span>结算总价&ensp;(元)</span>
        </div>
        <div
          @click="goBillRecord('2-1')"
          style="margin: 0rem 0px 0.8rem;"
        >
          <span class="f_20">{{ $route.query.selected == 1?Billdata.aiyoBalanceAmount : Billdata.wxBalanceAmount + Billdata.aliBalanceAmount | priceToFixed}}</span>
        </div>
        <div class="display settlementTotle">
          <div
            class="w50"
            @click="goBillRecord('2-2')"
          >
            <div>{{$route.query.billMonth?'已转账':'微信结算'}}</div>
            <div class="left">
              <div
                v-if="!$route.query.billMonth"
                :style="{display:(!$route.query.billMonth)?'flex':''}"
              >
                <div class="f84">{{ Billdata.wxBalanceAmount | priceToFixed }}
                  <span
                    class="c_normal f_m"
                    v-if="Billdata.status != 2 && $route.query.isshow == '1'"
                  >{{ Billdata.status == 0?"已结算":"已出账" }}</span>
                  <span
                    v-else-if="Billdata.status == 2 && $route.query.isshow == '1'"
                    class="c_normal f_m"
                    :class="Billdata.wxPayStatus == 2?'retry':''"
                    @click.stop="isTransfer(Billdata.wxPayStatus)"
                  >{{ wx_ali[Billdata.wxPayStatus] }}</span>
                </div>
              </div>
              <div v-else>
                <div class="f84">{{ Billdata.wxBalanceAmountSuccess + Billdata.aliBalanceAmountSuccess | priceToFixed }}</div>
                <div>微&emsp;信:<span class="c_000 mt6">&emsp;{{ Billdata.wxBalanceAmountSuccess | priceToFixed }}</span></div>
                <div>支付宝:<span class="c_000">&emsp;{{ Billdata.aliBalanceAmountSuccess | priceToFixed }}</span></div>
              </div>
            </div>
          </div>
          <div
            class="w50"
            @click="goBillRecord('2-3')"
          >
            <div>{{$route.query.billMonth?'未转账':'支付宝结算'}}</div>
            <div class="left">
              <div
                v-if='!$route.query.billMonth'
                :style="{display:(!$route.query.billMonth)?'flex':''}"
              >
                <div class="f84">{{ Billdata.aliBalanceAmount | priceToFixed }}
                  <span
                    v-if="Billdata.status != 2 && $route.query.isshow == '1'"
                    class="c_normal f_m"
                  >{{ Billdata.status == 0?"已结算":"已出账" }}</span>
                  <span
                    v-else-if="Billdata.status == 2 && $route.query.isshow == '1'"
                    class="c_normal f_m"
                    :class="Billdata.aliPayStatus == 2?'retry':''"
                    @click.stop="isTransfer(Billdata.aliPayStatus)"
                  >{{ wx_ali[Billdata.aliPayStatus] }}</span>
                </div>
              </div>
              <div v-else>
                <div
                  class="f84"
                  style="color: #ff5257;"
                >{{ Billdata.wxBalanceAmountFail + Billdata.aliBalanceAmountFail | priceToFixed }}</div>
                <div>微&emsp;信:<span class="c_000 mt6">&emsp;{{ Billdata.wxBalanceAmountFail | priceToFixed }}</span></div>
                <div>支付宝:<span class="c_000">&emsp;{{ Billdata.aliBalanceAmountFail | priceToFixed }}</span></div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- 收款详情 -->
      <div style="margin: 0.4rem 0.4rem 1rem;">
        <div
          class="mt6"
          v-if="Billorder"
        >
          <div
            class="title p5 back p65 c_000"
            style="font-size:0.8rem;background:#fff;border-radius: 8px 8px 0 0;"
          >
            <span
              class="three_color"
              style="background: #02BC6C;"
            ></span>
            <span>收款详情</span>
          </div>
          <div class="display p5 p65 m2">
            <div class="w30 left c_000">收款方式</div>
            <div class="w30 c_000">{{ $route.query.selected == 1?'间夜':'笔数' }}</div>
            <div class="w30 c_000">金额</div>
          </div>
        </div>
        <div
          class="back"
          v-if="Billorder"
        >
          <div class="display p5 border_b">
            <div
              class="w30 left c_000"
              @click="bthShow_1 =! bthShow_1"
            >平台收款
              <van-icon :name="bthShow_1 == false?'arrow':'arrow-down'" />
            </div>
            <div class="w30 no_bold">{{ Billorder.platCount }}</div>
            <div class="w30">{{ Billorder.platPay | priceToFixed }}</div>
          </div>
          <div
            v-if="bthShow_1"
            class="no_bold"
          >
            <div class="display p5 border_b">
              <div class="w30 left">哎哟有房</div>
              <div class="w30">{{ Billorder.aiyoPlatCount }}</div>
              <div
                class="w30 c_blue"
                @click="goBillRecord('3-1')"
              >{{ Billorder.aiyoPlatPay | priceToFixed }}</div>
            </div>
            <div class="display p5 border_b">
              <div class="w30 left">携程</div>
              <div class="w30">{{ Billorder.xcPlatCount }}</div>
              <div
                class="w30 c_blue"
                @click="goBillRecord('3-2')"
              >{{ Billorder.xcPlatPay | priceToFixed }}</div>
            </div>
            <div class="display p5 border_b">
              <div class="w30 left">美团</div>
              <div class="w30">{{ Billorder.mtPlatCount }}</div>
              <div
                class="w30 c_blue"
                @click="goBillRecord('3-3')"
              >{{ Billorder.mtPlatPay | priceToFixed }}</div>
            </div>
            <div class="display p5 border_b">
              <div class="w30 left">美团民宿</div>
              <div class="w30">{{ Billorder.mtmsPlatCount }}</div>
              <div
                class="w30 c_blue"
                @click="goBillRecord('3-3')"
              >{{ Billorder.mtmsPlatPay | priceToFixed }}</div>
            </div>
            <div class="display p5 border_b">
              <div class="w30 left">飞猪</div>
              <div class="w30">{{ Billorder.fzPlatCount }}</div>
              <div
                class="w30 c_blue"
                @click="goBillRecord('3-4')"
              >{{ Billorder.fzPlatPay | priceToFixed }}</div>
            </div>
            <div class="display p5 border_b">
              <div class="w30 left">其他平台</div>
              <div class="w30">{{ Billorder.otherPlatCount }}</div>
              <div
                class="w30 c_blue"
                @click="goBillRecord('3-5')"
              >{{ Billorder.otherPlatPay | priceToFixed }}</div>
            </div>
          </div>
        </div>
        <div
          class="back"
          v-if="Billorder"
        >
          <div
            class="display p5 border_b"
            @click="goBillRecord('线下收款')"
          >
            <!-- @click="bthShow_2 =! bthShow_2" -->
            <div class="w30 left c_000">线下收款
              <!-- <van-icon :name="bthShow_2 == false?'arrow':'arrow-down'" /> -->
            </div>
            <div class="w30 no_bold">{{ Billorder.normalCount }}</div>
            <div class="w30">{{ Billorder.normalPay | priceToFixed }}</div>
          </div>
          <!-- <div
            v-if="bthShow_2"
            class="no_bold"
          >
            <div class="display p5 border_b">
              <div class="w30 left">现金</div>
              <div class="w30">{{ Billorder.crashCount}}</div>
              <div
                class="w30 c_blue"
                @click="goBillRecord('4-1')"
              >{{ Billorder.crashPay | priceToFixed }}</div>
            </div>
            <div class="display p5 border_b">
              <div class="w30 left">微信</div>
              <div class="w30">{{ Billorder.wxCount }}</div>
              <div
                class="w30 c_blue"
                @click="goBillRecord('4-2')"
              >{{ Billorder.wxPay | priceToFixed }}</div>
            </div>
            <div class="display p5 border_b">
              <div class="w30 left">支付宝</div>
              <div class="w30">{{ Billorder.aliCount }}</div>
              <div
                class="w30 c_blue"
                @click="goBillRecord('4-3')"
              >{{ Billorder.aliPay | priceToFixed }}</div>
            </div>
            <div class="display p5 border_b">
              <div class="w30 left">银行卡</div>
              <div class="w30">{{ Billorder.posCount }}</div>
              <div
                class="w30 c_blue"
                @click="goBillRecord('4-4')"
              >{{ Billorder.posPay | priceToFixed }}</div>
            </div>
            <div class="display p5 border_b">
              <div class="w30 left">对公转账</div>
              <div class="w30">{{ Billorder.cooperCount }}</div>
              <div
                class="w30 c_blue"
                @click="goBillRecord('4-5')"
              >{{ Billorder.cooperPay | priceToFixed }}</div>
            </div>
          </div> -->
        </div>
        <div
          class="back"
          v-if="Billorder"
        >
          <div class="display p5 border_b">
            <div
              class="w30 left c_000"
              @click="bthShow_3 =! bthShow_3"
            >非房费收入
              <van-icon :name="bthShow_3 == false?'arrow':'arrow-down'" />
            </div>
            <div class="w30 no_bold">{{ $route.query.selected == 1?Billorder.otherCount:Billorder.depositCutCount }}</div>
            <div class="w30">{{$route.query.selected == 1?Billorder.otherPay:Billorder.depositCutPay | priceToFixed }}</div>
          </div>
          <div
            v-if="bthShow_3"
            class="no_bold"
          >
            <div
              class="display p5 border_b"
              v-if="$route.query.selected == 1"
            >
              <div class="w30 left">延时退房扣押</div>
              <div class="w30">{{ Billorder.autoDepositCutCount }}</div>
              <div
                class="w30 c_blue"
                @click="goBillRecord('5-1')"
              >{{ Billorder.autoDepositCutPay | priceToFixed }}</div>
            </div>
            <div class="display p5 border_b">
              <div class="w30 left">查房扣押</div>
              <div class="w30">{{ Billorder.depositCutCount }}</div>
              <div
                class="w30 c_blue"
                @click="goBillRecord('5-2')"
              >{{ Billorder.depositCutPay | priceToFixed }}</div>
            </div>
            <div
              class="display p5 border_b"
              v-if="$route.query.selected == 1"
            >
              <div class="w30 left">提前退房补差</div>
              <div class="w30">{{ Billorder.extraCount }}</div>
              <div
                class="w30 c_blue"
                @click="goBillRecord('5-3')"
              >{{ Billorder.extraPay | priceToFixed }}</div>
            </div>
          </div>
        </div>
        <div
          class="back"
          v-if="Billorder && $route.query.selected == 1"
        >
          <div class="display p5 border_b">
            <div
              class="w30 left c_000"
              @click="bthShow_4 =! bthShow_4"
            >支出款项
              <van-icon :name="bthShow_4 == false?'arrow':'arrow-down'" />
            </div>
            <div class="w30 no_bold">{{ Billorder.outCount }}</div>
            <div class="w30 c_blue">-{{ Billorder.outPay | priceToFixed }}</div>
          </div>
          <div
            v-if="bthShow_4"
            class="no_bold"
          >
            <div class="display p5 border_b">
              <div class="w30 left">房费冲账退款</div>
              <div class="w30">{{ Billorder.reverseCount }}</div>
              <div
                class="w30 c_blue"
                @click="goBillRecord('6-1')"
              >-{{ Billorder.reversePay | priceToFixed }}</div>
            </div>
          </div>
        </div>
        <!-- 合计 -->
        <div
          class="back_e2"
          v-if="Billorder"
        >
          <div class="display p5 p65 m2">
            <div class="w30 left c_000">合计</div>
            <div class="w30 c_000">{{ Billorder.totalCount }}</div>
            <div class="w30 c_000">{{ Billorder.totalPay | priceToFixed }}</div>
          </div>
        </div>
      </div>
    </div>
    <noPaidDetails v-else-if="$route.query.type == 'notPaid'"></noPaidDetails>
  </div>
</template>
<script>
import '@/assets/styles/global.css';
import { GetBusinessBillRecordSumHotelMonth, GetBusinessMonthBillRecordSumbyBillRecord, GetBusinessMonthBillRecordSumHotelMonth } from '@/api/api_bill.js';
import { GetBusinessBillRecordSumByBillRecord, GetBusinessBillRecordSumDetailAll, GetBusinessBillRecordTransfer, GetBusinessMonthBillRecordTransfer } from '@/api/api_business_hotel_and_scale';
import { MessageBox, Toast } from 'mint-ui';
import noPaidDetails from './noPaidDetails';
import longDetails from './longDetails';
export default {
    components: {
        noPaidDetails,
        longDetails
    },
    data() {
        return {
            bthShow_1: true,
            bthShow_2: true,
            bthShow_3: true,
            bthShow_4: true,
            Billdata: null,
            Billorder: null,
            longType: [
                { name: '租金', price: '0' },
                { name: '水费', price: '0' },
                { name: '电费', price: '0' },
                { name: '气费', price: '0' },
                { name: '清洁费', price: '0' }
            ],
            wx_ali: { 0: '未转账', 1: '已转账成功', 2: '重试' }
        };
    },
    created() {
        this.$store.commit('setHeader', `${this.$route.query.time || this.$route.query.billMonth || this.$route.query.billStartTime || '未出账'}账单详情`);
        this.getBillRecord(this.$route.query.billMonth || this.$route.query.billRecordId || this.$route.query.monthBillRecordId);
    },
    filters: {
        priceToFixed(price) {
            return Number(price).toFixed(2);
        }
    },
    methods: {
        // 根据账单查询明细
        getBillRecord() {
            if (this.$route.query.billMonth && this.$route.query.selected == 1) {
                let data = {
                    hotelId: this.$route.query.hotelId,
                    billMonth: this.$route.query.billMonth
                };
                GetBusinessBillRecordSumHotelMonth(data).done((res) => {
                    this.Billdata = res.data;
                    this.Billorder = res.order;
                });
            } else if (this.$route.query.billMonth && this.$route.query.selected == 2) {
                let data = {
                    hotelId: this.$route.query.hotelId,
                    billMonth: this.$route.query.billMonth
                };
                GetBusinessMonthBillRecordSumHotelMonth(data).done((res) => {
                    this.Billdata = res.data;
                    this.Billorder = res.order;
                    this.longType[0].price = this.Billdata.roomAmount;
                    this.longType[1].price = this.Billdata.waterPrice;
                    this.longType[2].price = this.Billdata.elecAmount + this.Billdata.elecPrice;
                    this.longType[3].price = this.Billdata.gasPrice;
                    this.longType[4].price = this.Billdata.rubbishPrice;
                });
            } else if (this.$route.query.isshow == '1') {
                if (this.$route.query.selected == 1) {
                    GetBusinessBillRecordSumByBillRecord(this.$route.query.billRecordId).done((res) => {
                        this.Billdata = res.data;
                        this.Billorder = res.order;
                    });
                } else {
                    GetBusinessMonthBillRecordSumbyBillRecord({ monthBillRecordId: this.$route.query.monthBillRecordId }).then((res) => {
                        this.Billdata = res.data;
                        this.Billorder = res.order;
                        this.longType[0].price = this.Billdata.roomAmount;
                        this.longType[1].price = this.Billdata.waterPrice;
                        this.longType[2].price = this.Billdata.elecAmount + this.Billdata.elecPrice;
                        this.longType[3].price = this.Billdata.gasPrice;
                        this.longType[4].price = this.Billdata.rubbishPrice;
                    });
                }
            } else if (this.$route.query.isshow == '0') {
                GetBusinessBillRecordSumDetailAll({ billTime: this.$route.query.billStartTime }).done((res) => {
                    this.Billdata = this.$route.query.Billdata;
                    this.Billorder = res.data;
                });
            }
        },
        // 跳转至订单列表
        goBillRecord(platNum) {
            if (this.$route.query.isshow == '1') {
                this.$router.push({
                    path: '/moneyDetail',
                    query: {
                        billRecordId: this.$route.query.billRecordId || this.$route.query.monthBillRecordId,
                        selected: this.$route.query.selected,
                        platNum: platNum
                    }
                });
            }
        },
        // 重新转账
        isTransfer(status) {
            if (status == 2) {
                MessageBox.confirm('', {
                    message: '是否重新转账？',
                    title: '提示',
                    cancelButtonText: '否',
                    confirmButtonText: '是'
                })
                    .then((action) => {
                        if (action == 'confirm') {
                            let _api = this.$route.query.selected == 1 ? GetBusinessBillRecordTransfer : GetBusinessMonthBillRecordTransfer;
                            let _id = this.$route.query.selected == 1 ? this.$route.query.billRecordId : this.$route.query.monthBillRecordId;
                            _api(_id).done((res) => {
                                Toast({
                                    message: res.msg,
                                    duration: 900
                                });
                                this.getBillRecord();
                            });
                        }
                    })
                    .catch((e) => {
                        console.log(e);
                    });
            }
        }
    }
};
</script>
<style scoped>
#finance {
    font-size: 0.8rem;
    text-align: left;
    color: #5c5c5c;
    position: fixed;
    top: 56px;
    bottom: 0;
    left: 0;
    right: 0;
    overflow-y: scroll;
}
.three_color {
    display: inline-block;
    height: 10px;
    width: 4px;
    background: #048fe5;
    border-radius: 2px;
}
.f_20 {
    font-size: 1.4rem;
    color: #000;
    font-weight: bold;
}
.w50 {
    width: 50%;
}
.w24 {
    width: 22%;
}
.w50 {
    width: 50%;
    text-align: left;
    display: flex;
    flex-direction: column;
}
.priceBox {
    font-weight: 450;
    background: #fff;
    margin: 0.4rem;
    border-radius: 8px;
}
.handBill {
    display: inline-block;
    width: 50px;
    height: 22px;
    color: #fff;
    font-size: 0.7rem;
    background: #2e7bf7;
    border-radius: 12px;
    display: flex;
    justify-content: center;
    line-height: 23px;
    margin-right: 20px;
}
.title {
    font-size: 0.8rem;
}
.display {
    text-align: center;
    display: flex;
    justify-content: space-between;
}
.p6 {
    padding: 0 0.6rem;
}
.m6 {
    margin: 0 0.6rem;
}
.mt6 {
    margin-top: 0.6rem;
}
.m2 {
    margin: 0 0.2rem;
}
.p5 {
    padding: 0.5rem 0;
}
.p0 {
    padding: 0;
}
.p65 {
    padding: 0.5rem 0.6rem;
}
.w30 {
    text-align: center;
    width: 30%;
}
.w30_l {
    width: 36%;
    text-align: center;
}
.c_blue {
    color: #048fe5;
    text-align: center;
}
.f84 {
    font-size: 0.84rem;
}
.left {
    text-align: left;
}
.c_000 {
    color: #000;
}
.border_b {
    border-bottom: 1px solid #e5e5e5;
    margin: 0 0.8rem;
}
.no_bold {
    font-weight: normal;
}
.c_98 {
    font-size: 0.7rem;
    text-align: left;
    color: #989898;
    font-weight: normal;
    padding-bottom: 0.1rem;
}
.back {
    font-weight: bold;
    background: #fff;
    font-size: 0.7rem;
}
.back_e2 {
    background: #e2e2e2;
    font-weight: bold;
    font-size: 0.84rem;
    border-radius: 0 0 8px 8px;
}
.nextBtn {
    width: 90%;
    margin: 20px auto;
    background-color: #f60;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
}
.f_m {
    font-size: 0.6rem;
    margin: 0.1rem auto;
}
.c_normal {
    color: #02bc6c;
    font-weight: normal;
}
.retry {
    border: 1px solid #ff5257;
    border-radius: 10px;
    color: #ff5257;
    font-weight: normal;
    display: inline-block;
    width: 2.2rem;
    text-align: center;
}
.settlementTotle {
    color: #989898;
    text-align: left;
    font-size: 0.7rem;
    line-height: 26px;
}
.aaa {
    display: inline-block;
    width: 33%;
    margin-bottom: 20px;
    text-align: center;
}
</style>



